<template>
  <loading :active="isLoading"></loading>
  <pagesBanner></pagesBanner>
  <section class="news_detail pages position-relative overflow-hidden">
    <div class="container position-relative z-index-1 box_">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link to="/news" itemprop="item" title="最新消息">
              <span itemprop="name">最新消息</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link :to="`/news/${news.id}`" itemprop="item" :title="news.title">
              <span itemprop="name">{{news.title}}</span>
            </router-link>
            <meta itemprop="position" content="3" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 py-lg-5 py-4">
        <div class="px-lg-5">
          <div class="row align-items-end">
            <div class="col-md mb-lg-4 mb-2">
              <h3 class="mb-0 ps-4 py-2">{{news.title}}</h3>
            </div>
            <div
              class="date col-md-auto py-2 mb-lg-4 mb-2 d-flex align-items-center"
            >
              <img src="@/assets/img/clock.svg" alt="" class="me-2" />{{ $filters.date(news.create_at)}}
            </div>
          </div>
          <div class="editor pb-5">
            <img :src="`./img/news_detail.jpg`" alt="" class="mb-4"/>
            {{news.content}}
          </div>
          <div class="text-center btn-box">
            <router-link to="/news" replace class="btn">
              回上一頁
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import pagesBanner from '../components/pagesBanner.vue'
export default {
  components: {
    pagesBanner
  },
  data () {
    return {
      isLoading: false,
      news: {},
      id: ''
    }
  },
  methods: {
    getData () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/article/${this.id}`
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            this.news = res.data.article
          }
        })
    },
    simpleScrollTrigger () {
      this.gsap.from('.box_', {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.5,
        scrollTrigger: {
          trigger: '.box_',
          start: 'top 90%'
        }
      })
    }
  },
  created () {
    this.id = this.$route.params.newsId
    this.getData()
  },
  mounted () {
    this.simpleScrollTrigger()
  }
}
</script>
